<template>
  <v-layout row>
    <v-flex xs12 sm12 lg12>
      <v-card flat>
        <v-toolbar color="cyan" dark extended>
          <template v-slot:extension>
            <v-toolbar-title style="margin-left:0px;font-weight: 700 !important;">USD</v-toolbar-title>
            <v-spacer></v-spacer>

            <v-toolbar-title style="font-weight: 700 !important;">
              <v-dialog v-model="dialog" width="500">
                <template v-slot:activator="{ on }">
                  <v-btn flat v-on="on">{{usd}}</v-btn>
                </template>

                <v-card>
                  <v-card-text>
                    <v-text-field
                      label="Input the Amount*"
                      type="number"
                      v-model="usd"
                      autofocus
                      prefix="$"
                      :mask="mask"
                      maxlength="10"
                    ></v-text-field>
                  </v-card-text>
                </v-card>
              </v-dialog>
            </v-toolbar-title>
          </template>
          <v-toolbar-title>USD - United States Dollar</v-toolbar-title>
        </v-toolbar>
        <v-container fluid grid-list-lg>
          <v-layout row wrap>
            <v-flex xs12 v-for="(item, index) in list" :key="index">
              <v-card>
                <v-card-title primary-title>
                  <v-flex xs12>
                    <v-layout align-start justify-space-between row fill-height>
                      <div class="headline">{{item}}</div>
                      <div class="headline" style="float:right;">{{calculate(item)}}</div>
                    </v-layout>
                  </v-flex>
                  <v-flex>
                    <v-layout align-start justify-center column>
                      <div>{{item}} - {{currency[item]}}</div>
                      <div>1 USD = {{item}} {{rates[item]}}</div>
                    </v-layout>
                  </v-flex>
                </v-card-title>
                <v-card-actions>
                  <v-layout align-center justify-end>
                    <v-btn flat color="red darken-2" @click="remove(index)">Remove</v-btn>
                  </v-layout>
                </v-card-actions>
              </v-card>
            </v-flex>
            <v-flex xs12>
              <v-btn block color="cyan" dark v-if="!show" @click="show = true">Add</v-btn>
              <v-select
                :items="items"
                label="Select Currency"
                v-model="selected"
                solo
                attach
                v-if="show"
                @change="getRate()"
                style="margin: 6px 0;"
              ></v-select>
            </v-flex>
          </v-layout>
        </v-container>
      </v-card>
    </v-flex>
  </v-layout>
</template>

<script>
import HelloWorld from "./components/HelloWorld";
import axios from "axios";
export default {
  data() {
    return {
      items: [
        "USD",
        "CAD",
        "IDR",
        "GBP",
        "CHF",
        "SGD",
        "INR",
        "MYR",
        "JPY",
        "KRW"
      ],
      currency: {
        USD: "US Dollar",
        CAD: "Canadian Dollar",
        IDR: "Rupiah",
        GBP: "Pound Sterling",
        CHF: "Swiss Franc",
        SGD: "Singapore Dollar",
        INR: "Indian Rupee",
        MYR: "Malaysian Ringgit",
        JPY: "Yen",
        KRW: "South Korean Won"
      },
      selected: "",
      rates: [],
      usd: "1",
      show: false,
      list: [],
      mask: "##########"
    };
  },
  methods: {
    getRate() {
      this.list.push(this.selected);
      this.show = false;
      this.selected = "";
    },
    calculate(currency) {
      var rate = this.rates[currency];
      rate = this.usd * rate;
      return rate;
    },
    remove(index) {
      this.list.splice(index, 1);
    }
  },
  mounted() {
    axios.get("https://api.exchangeratesapi.io/latest?base=USD").then(data => {
      console.log(data);
      this.rates = data.data.rates;
    });
  }
};
</script>
